import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';

import {RolesService} from "src/app/services/roles.service";
import { Role } from 'src/app/models/role';



@Component({
    selector: 'app-board-admin',
    templateUrl: './board-admin.component.html',
    styleUrls: ['./board-admin.component.scss'],
    standalone: false
})
export class BoardAdminComponent implements OnInit {
  private gridApi: any;

  rowData: any;
  allRoles: any;

  private roles!: string[];
  columnDefs : any;

  constructor(private userService: UserService, private roleService: RolesService ) {
                this.allRoles = this.getAllRoles();
              }

  ngOnInit(): void {



    this.userService.getAdminBoard().subscribe(
      (data : any) => {
        this.rowData = data;
      },
      (err : any)=> {
        this.rowData = JSON.parse(err.error).message;
      }

    );

    // this.allRoles.forEach( (item) => {
    //   this.roles.push(item.name as string);
    // });



    this.columnDefs = [
      {headerName: 'User name', field: "username" },
      { headerName: 'Email', field: "email" },
      { headerName: 'Roles',
        field: "roles",
        editable:true,

        cellEditorParams: {
          allRoles : this.allRoles
        },

        // cellRendererParams: {

        //   values: this.allRoles
        // }
      }];


  }

  getAllRoles():any{
    let returnValue:Role[];
    returnValue = [];

    this.roleService.getAllRoles().subscribe(
      (data : any) => {
        returnValue = data;
      },
      (err : any)=> {
        returnValue = JSON.parse(err.error).message;
      }
    );
    return returnValue;
  }



  ngOnGridReady(params:any) {
    this.gridApi = params.api;
    //this.gridApi.sizeColumnsToFit();
  }
}
